/** @format */

.hero {
  margin-top: 155px;
  margin-bottom: 113px;
  display: flex;
  justify-content: center;
  gap: 120px;
}
.hero > .first_content {
  max-width: 488px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-shrink: 0;
}
.hero > .first_content > div {
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;
}
.hero > .first_content > div > span {
  font-weight: 700;
  font-size: 70px;
  line-height: 100%;
  text-align: center;
  color: #00227f;
  margin: 0;
  text-align: start;
  display: flex;
  flex-direction: column;
}
.text_center {
  text-align: center;
}
.hero > .first_content > p {
  margin: 0;
  text-align: left;
  font-weight: 400;
  font-size: 18px;
  line-height: 36px;
  color: #000920;
  margin-bottom: 50px;
}
.hero > .first_content > button {
  background: linear-gradient(180deg, rgba(172, 0, 253, 0.85) 0%, #9100d5 100%);
  border-radius: 24px;
  width: 256px;
  height: 67px;
  align-self: flex-start;
  padding: 16px 48px;
  font-weight: 700;
  font-size: 20px;
  line-height: 25px;
  color: #fbfcff;
  cursor: pointer;
  transition: 0.25s all;
  border: none;
}
.hero > .first_content > button:hover {
  opacity: 0.8;
  transform: translateY(-2px);
}
.hero > .last_content {
  max-width: 384px;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
  align-content: flex-start;
  gap: 20px;
}
.hero > .last_content > span {
  background: #dfe8ff;
  box-shadow: 0px 4px 8px rgba(0, 9, 32, 0.16);
  border-radius: 24px 24px 24px 0px;
  transform: matrix(-1, 0, 0, 1, 0, 0);
  width: 182px;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.hero > .last_content > span:nth-child(2) {
  border-radius: 24px 24px 0px 24px;
}
.hero > .last_content > span:nth-child(3) {
  border-radius: 0px 24px 24px 24px;
}
.hero > .last_content > span:nth-child(4) {
  border-radius: 24px 0px 24px 24px;
}

@media screen and (max-width: 1100px) {
  .hero {
    margin-top: 10%;
    margin-bottom: 20%;
    flex-direction: column;
    gap: 5vh;
    align-items: center;
    justify-content: center;
  }
  .hero > .first_content {
    max-width: 100%;
    align-items: center;
  }
  .hero > .first_content > div {
    margin-bottom: 20px;
    align-items: center;
  }
  .hero > .first_content > div > span {
    font-size: 6vw;
    line-height: 0.8;
  }
  .hero > .first_content > p {
    margin: 0 20px;
    text-align: center;
    font-weight: 400;
    font-size: 3.5vw;
    line-height: 150%;
    margin-bottom: 20px;
  }
  .hero > .first_content > button {
    width: 50%;
    height: 50px;
    align-self: center;
    padding: 8px 24px;
    font-size: 2vw;
  }
  .hero > .last_content {
    width: 100%;
    align-items: center;
    justify-content: center;
    align-content: center;
    gap: 10px;
  }
  .hero > .last_content > span {
    width: 45%;
    height: 150px;
  }
  .hero > .last_content > span > img {
    height: 100px;
  }
}
